:root {
  --bg-body-light: rgb(204,204,204);
  --color-main-light: #000000 ;
  --color-secondary-light: #ffffff;

  --bg-body-dark: #2a2929;
  --color-main-dark: #ffffff;
  --color-secondary-dark: #000000;

  --bg-body: var(--bg-body-dark);
  --color-main: var(--color-main-dark);
  --color-secondary: var(--color-secondary-dark);

  --transition: all 0.2s ease;
}


$primary-color: #3d55a1;
$effect-color: #5b955b;
$error-color: #9f0e0e;
$bg-gold:#827056;

$border-radius-m: 15px;
$border-radius-l: 25px;
$border-radius-xl: 35px;

@mixin flex-al-center {
  display: flex;
  align-items: center;
}
@mixin flex-al-jc-center {
  display: flex;
  align-items: center;
  justify-content: center;
}
@mixin flex-column {
  display: flex;
  flex-direction: column;
}

@mixin flex-al-center-column {
  display: flex;
  align-items: center;
  flex-direction: column;
}

@mixin text-shadow{
  text-shadow: 2px 2px 2px black
};

%games-list_grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  grid-auto-flow: row;
  min-width: 250px;
  max-width: 100%;
  flex-wrap: wrap;
  gap: 30px;
  text-align: center;
  @media(max-width: 768px) {
    grid-template-columns: 1fr;
  }
}

*, *::after, *::before {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  color:var(--color-main);
  transition: background-color 0.3s linear, color 0.5s linear;
}


body {
  position: relative;
  overflow-x: hidden;
  margin: 0 calc(-1 * (100vw - 100%)) 0 0;
  min-height: 100vh;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
  'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
  sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: var(--bg-body) ;
  color:var(--color-main);
}


button, a{
  cursor: pointer;
  outline: none;
}

a {
  text-decoration: none;
  color:var(--color-main);
}

ul {
  list-style: none;
}

.App {
  display: flex;
  flex-direction: column;
  max-width: 1180px;
  margin: 0 auto;
  padding: 0 20px 0;
  min-height: 100vh;
  @media (max-width: 840px){
    position: relative;
    padding-top: 90px;
  }
}

main{
  position: relative;
  flex-grow: 1;
  padding: 20px 0 20px;
}

/* Firefox */
body {
  scrollbar-width: thin;
  scrollbar-color: steelblue #7B8182;
}

/* Chrome, Edge and Safari */
*::-webkit-scrollbar {
  width: 8px;
}
*::-webkit-scrollbar-track {
  border-radius: 4px;
  background-color: #7B8182;
}

*::-webkit-scrollbar-track:hover {
  background-color: #A4AAAC;
}

*::-webkit-scrollbar-track:active {
  background-color: #A4AAAC;
}

*::-webkit-scrollbar-thumb {
  border-radius: 5px;
  background-color: steelblue;
}

*::-webkit-scrollbar-thumb:hover {
  background-color: steelblue;
}

*::-webkit-scrollbar-thumb:active {
  background-color: darkred;
}

.visually-hidden{
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
